async function deconnecter_fn() {
  var audio = new Audio('../assets/aud/exit.ogg');
  audio.play();
        localStorage.removeItem("connecteid");
        localStorage.removeItem("connectmdp");
        localStorage.removeItem("connectnom");
        window.location.href = '/connecter' ;

} ;


async function backend_tester_fn() {
  var eid = localStorage.getItem("connecteid");
  var motdepasse = localStorage.getItem("connectmdp");
  const rep = await fetch(`http://127.0.0.1:8000/gestionemployes/backend_connecter_bd?eid=${eid}&motdepasse=${motdepasse}`) ;   
  if (rep.ok){
    rep.json().then((data)=>{ 
      if (data.length != 0) {
        if (data[0].admin == 1) {
          window.location.href = '/accueiladmin' ;
        }
        if (data[0].admin == 0) {
          (document.getElementById("connectnomm") as HTMLFormElement).insertAdjacentHTML("afterbegin",data[0].nom+" ") ;
        }
      }
      else {
        localStorage.removeItem("connecteid");
        localStorage.removeItem("connectmdp");
        localStorage.removeItem("connectnom");
        window.location.href = '/connecter' ;
      }
    }) 
  } else {
    localStorage.removeItem("connecteid");
    localStorage.removeItem("connectmdp");
    localStorage.removeItem("connectnom");
    window.location.href = '/connecter' ;
  }
} ;

async function backend_consulter_fn() {
  const rep = await fetch(`http://127.0.0.1:8000/gestionemployes/backend_consulter_bd`) ;   

  if (rep.ok){
    rep.json().then((data)=>{ 
      var table = document.getElementById('table') ;

      (table as HTMLFormElement).innerHTML = `    <tbody id="table">
      <tr>
        <td> </td>
        <td> </td>
        <td> </td>
        <td> </td>
        <td> </td>
        <td> </td>
        <td> </td>
        <td> </td>
        <td> </td>
      </tr>

    </tbody>` ;
          for(var i=0 ; i<data.length;i++) {
        (table as HTMLFormElement).insertAdjacentHTML("beforeend",` <tr> <td> <img src="`+data[i].photo+`"  width="30" height="30" /> </td>  <td> ${data[i].eid} </td>  <td> ${data[i].cin} </td> <td> ${data[i].prenom} </td>  <td> ${data[i].nom} </td>  <td> ${data[i].ville} </td>  <td> ${data[i].numtel} </td>  <td> ${data[i].email} </td>   <td> ${data[i].salaire} </td></tr>` )
      }
    })
  }

  var audio = new Audio('../assets/aud/loaded.ogg');
audio.play();
} ;



